import { Component } from 'react';
import { getUserInfo } from '../Services/users';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      error: false,
    };
  }

  componentDidMount() {
    if (!Cookies.get("idUser") || !Cookies.get("token")) {
      window.location.href = "/application";
    } else {
      getUserInfo(Cookies.get("idUser"), Cookies.get("token"))
        .then(data => {
          if (data.find) {
            this.setState({ isLoading: false });
            switch (data.result.level) {
              case 3:
                window.location.href="/application/sessions/inProgress";
                break;
              case 2:
                window.location.href="/application/stocks";
                break;
              case 1:
                window.location.href="/application/stocks";
                break;
              default: 
                Cookies.remove('idUser');
                Cookies.remove('token');
                window.location.href = '/application/';
                break;
            }
          } else if (data.invalidToken || data.userNotFound) {
            Cookies.remove('idUser');
            Cookies.remove('token');
            window.location.href = '/application/';
          } else {
            this.setState({ isLoading: false, error: true });
          }
        })
        .catch(() => {
          this.setState({ isLoading: false, error: true });
        });
    }
  }

  render() {
    return this.state.isLoading ? <Spinner animation = 'border' role = 'status'><span className='sr-only'>Loading...</span></Spinner>  : null;
  }
}

export default Home;