import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import _ from 'underscore';
import Cookies from 'js-cookie';
import { addCategory } from '../Services/guichet';

class PopupAddClient extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      name: '',
      type: 'enter',

      isLoading: false,
      success: false,
      invalidForm: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleName = (event) => {
    this.setState({ name: event.target.value });
  }

  _handleType = (event) => {
    this.setState({ type: event.target.value });
  }

  _displayButton = () => {
    if ((this.state.type.length >= 2 && this.state.type.length <= 70 && _.isString(this.state.type) && (this.state.type === 'enter' || this.state.type === 'outlet')) && (_.isString(this.state.name) && this.state.name.length >= 2 && this.state.name.length <= 70 )) {
      return <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button>
    } else {
      return <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = {
      name: this.state.name,
      type: this.state.type,
    };

    addCategory(toSend, Cookies.get('token'))
      .then(res => {
        if (res.create) {
          this.setState({ isLoading: false, success: true, invalidForm: false, existName: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, existName: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Catégorie ajouté avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter une catégorie</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Nom: </Form.Label>
            <Form.Control
              onChange={this._handleName}
              value={this.state.name}
              type="text"
              placeholder="Le nom de la catégorie..."
              aria-label="Nom"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="type">
            <Form.Label>Type: </Form.Label>
            <select onChange={this._handleType} className="form-control">
              <option value="enter" selected>Entrée</option>
              <option value="outlet">Sortie</option>
            </select>
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupAddClient;