import React from 'react';
import { Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import { getAllProducts } from '../Services/products';
import MaterialTable from "material-table";
import Cookie from 'js-cookie';
import _ from 'lodash';

class Stocks extends React.Component {
  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Stocks';
    this.state = {
      info: [],
      toDisplay: [],
      isLoading: true,
      error: false,
      type: "all",
    };
    this.columns = [
      {
        title: "Nom",
        field: "name",
      },
      {
        title: "Stock",
        field: "inStock",
      },
    ];
  }

  componentDidMount() {
    getAllProducts(Cookie.get('token'))
      .then(data => {
        if (data.find) {
          this.setState({ info: data.result, toDisplay: data.result, isLoading: false, error: false });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(() => {
        this.setState({ error: true });
      });
  }

  _handleType= (event) => {
    this.setState({ type: event.target.value }, () => {
      this.state.type = event.target.value;
      if (this.state.type === "all") {
        this.setState({ toDisplay: this.state.info });
      } else {
        const until = _.filter(this.state.info, (o) => (o.type === this.state.type));
        this.setState({ toDisplay: until });
      }
    });
  }

  _displayType = () => {
    return (
      <>
        Type:{' '}
        <select onChange={this._handleType}>
          <option value="all" selected>Tout</option>
          <option value="Boissons">Boissons</option>
          <option value="Plâts">Plâts</option>
          <option value="Spiritueux">Spiritueux</option>
          <option value="Cigarettes">Cigarettes</option>
        </select>
      </>
    )
  }

  render() {
    if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Stocks</h1>
            <em>Une erreur a eu lieu, réessayez plus tard ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Stocks</h1>
            {this._displayType()}<br /><br />
            <a href={`/application/print/${this.state.type}`} target="_blank" className="btn btn-primary">Imprimer</a><br /><br />
            <MaterialTable
              title='Stocks'
              data={this.state.toDisplay}
              columns={this.columns}
              options={{
                search: true,
                paging: false,
                filtering: true,
                exportButton: true,
              }}
              isLoading={this.state.isLoading}
            />
          </Container>
        </>
      );
    }
  }
}

export default Stocks;
