import React from 'react';
import Cookies from 'js-cookie';
import MaterialTable from "material-table";
import EditIcon from '@material-ui/icons/Edit';
import { getAllCategories } from '../Services/guichet';

class Tablecategories extends React.Component {
  constructor() {
    super();
    this.state = {
      categories: [],
      isLoading: true,
      error: false,
    };

    this.columns = [
      {
        title: "Nom",
        field: "name",
      },
      {
        title: "Type",
        field: "type2",
      },
    ];
  }

  componentDidMount() {
    getAllCategories(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          let until= [];
          for (let index in data.result) {
            until.push({ ...data.result[index], type2: this._adapt(data.result[index].type) });
          }
          this.setState({ categories: until, isLoading: false, error: false });
        } else if (data.invalidToken) {
          Cookies.remove('idUser');
          Cookies.remove('token');
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, error: false });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, error: false });
      });
  }

  _adapt = (toAdapt) => {
    return toAdapt === 'enter' ? "Entrée" : "Sortie";
  }

  _handleUpdate = (event, rowData) => {
    let capture;
    for (let index in this.state.categories) {
      if (this.state.categories[index].idCategory === rowData.idCategory) {
        capture= this.state.categories[index];
      }
    }
    this.props.onUpdate(capture);
  }

  render() {
    if (this.state.error) {
      return <h2>Une erreur a eu leiu veuillez réessayer</h2>
    } else {
      return (
        <MaterialTable
          title='Liste des categories'
          data={this.state.categories}
          columns={this.columns}
          options={{
            search: true,
            paging: false,
            filtering: true,
            exportButton: true,
          }}
          isLoading={this.state.isLoading}
          actions={[
            {
              icon: () => <EditIcon htmlColor='#007bff' />,
              tooltip: "Modifier",
              onClick: this._handleUpdate,
            },
          ]}
        />
      );
    }
  }
}

export default Tablecategories;