import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import _ from 'underscore';
import Cookies from 'js-cookie';
import { updateProducts } from '../Services/products';

class PopupEditProducts extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      name: this.props.data.name,
      price: parseInt(this.props.data.price),
      type: this.props.data.type,
      unit: this.props.data.unit,
      buyPrice: this.props.data.buyPrice,

      isLoading: false,
      success: false,
      invalidForm: false,
      existName: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleName = (event) => {
    this.setState({ name: event.target.value });
  }

  _handlePrice = (event) => {
    this.setState({ price: event.target.value });
  }

  _handleType = (event) => {
    this.setState({ type: event.target.value });
  }

  _handleUnit = (event) => {
    this.setState({ unit: event.target.value });
  }

  _handleBuyPrice  = (event) => {
    this.setState({ buyPrice: event.target.value });
  }

  _displayButton = () => {
    if ((this.state.name.length >= 2 && this.state.name.length <= 70) && (this.state.type === 'Spiritueux' || this.state.type === 'Boissons' || this.state.type === 'Plâts' || this.state.type === 'Cigarettes') && (_.isNumber(Number(this.state.price))) && (_.isNumber(Number(this.state.inStock))) && (this.state.unit.length >= 2 && this.state.unit.length <= 70) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}>Confirmer</Button>
    } else {
      return <Button variant="success" disabled>Confirmer</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = {
      name: this.state.name,
      price: Number(this.state.price),
      type: this.state.type,
      unit: this.state.unit,
      buyPrice: this.state.buyPrice,
    };

    updateProducts(Cookies.get('token'), this.props.data.idProduct, toSend)
      .then(res => {
        if (res.update) {
          this.setState({ isLoading: false, success: true, invalidForm: false, existName: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, existName: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.existName) {
          this.setState({ isLoading: false, success: false, invalidForm: false, existName: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Produit modifié avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.existName) {
      return <Alert variant="warning">Un produit possède déjà le même nom</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  _displayOption = () => {
    if (this.state.type === 'Spiritueux') {
      return (
        <>
          <option value='Spiritueux' selected>Spiritueux</option>
          <option value='Cigarettes'>Cigarettes</option>
          <option value='Boissons'>Boissons</option>
          <option value='Plâts'>Plâts</option>
        </>
      );
    } else if (this.state.type === 'Boissons') {
      return (
        <>
          <option value='Spiritueux'>Spiritueux</option>
          <option value='Cigarettes'>Cigarettes</option>
          <option value='Boissons' selected>Boissons</option>
          <option value='Plâts'>Plâts</option>
        </>
      );
    } else if (this.state.type === 'Plâts') {
      return (
        <>
          <option value='Spiritueux'>Spiritueux</option>
          <option value='Cigarettes'>Cigarettes</option>
          <option value='Boissons'>Boissons</option>
          <option value='Plâts' selected>Plâts</option>
        </>
      );
    } else if (this.state.type === 'Cigarettes') {

      return (
        <>
          <option value='Spiritueux'>Spiritueux</option>
          <option value='Cigarettes' selected>Cigarettes</option>
          <option value='Boissons'>Boissons</option>
          <option value='Plâts'>Plâts</option>
        </>
      );
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Modifier un produit</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Nom: </Form.Label>
            <Form.Control
              onChange={this._handleName}
              value={this.state.name}
              type="text"
              placeholder="Le nom du produit..."
              aria-label="Nom"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="type">
            <Form.Label>Type: </Form.Label>
            <select id='type' className='form-control' onChange={this._handleType}>
              {this._displayOption()}
            </select>
          </Form.Group>
          <Form.Group controlId="price">
            <Form.Label>Prix (en $): </Form.Label>
            <Form.Control
              onChange={this._handlePrice}
              value={this.state.price}
              type="number"
              placeholder="Le prix du produit..."
              aria-label="price"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="unit">
            <Form.Label>Unité (Portions, Bouteille, Assiette): </Form.Label>
            <Form.Control
              onChange={this._handleUnit}
              value={this.state.unit}
              type="text"
              placeholder="L'unité du produit..."
              aria-label="unit"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="buyPrice">
            <Form.Label>Prix d'achat commun (en $): </Form.Label>
            <Form.Control
              onChange={this._handleBuyPrice}
              value={this.state.buyPrice}
              type="number"
              placeholder="Le prix d'achat du produit..."
              aria-label="buyPrice"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupEditProducts;