import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import PasswordInput from './PasswordInput';
import passwordValidator from 'password-validator';
import _ from 'underscore';
import { addUser } from '../Services/users';
import Cookies from 'js-cookie';

class PopupAddUser extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      name: '',
      pseudo: '',
      password: '',
      level: 1,

      isLoading: false,
      success: false,
      invalidForm: false,
      existPseudo: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleName = (event) => {
    this.setState({ name: event.target.value });
  }

  _handlePseudo = (event) => {
    this.setState({ pseudo: event.target.value });
  }

  _handlePassword = (event) => {
    this.setState({ password: event.target.value });
  }
  
  _handleLevel = (event) => {
    this.setState({ level: parseInt(event.target.value) });
  }

  _displayButton = () => {
    const schema = new passwordValidator();// On crée une nouvelle instance de l'objet
    schema// On crée un nouveau schéma
      .is().min(8)                                    // Minimum length 8
      .has().uppercase()                              // Must have uppercase letters
      .has().lowercase()                              // Must have lowercase letters
      .has().digits(2)                                // Must have at least 2 digits
    
    if (schema.validate(this.state.password) && (this.state.name.length >= 2 && this.state.name.length < 30 && _.isString(this.state.name)) && (this.state.pseudo.length >= 2 && this.state.pseudo.length < 30 && _.isString(this.state.pseudo)) && (this.state.level >= 1 && this.state.level <= 3) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button>
    } else {
      return <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = {
      name: this.state.name,
      pseudo: this.state.pseudo,
      password: this.state.password,
      level: this.state.level,
    };

    addUser(toSend, Cookies.get('token'))
      .then(res => {
        if (res.create) {
          this.setState({ isLoading: false, success: true, invalidForm: false, existPseudo: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, existPseudo: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.existPseudo) {
          this.setState({ isLoading: false, success: false, invalidForm: false, existPseudo: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, existPseudo: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, existPseudo: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Utilisateur ajouté avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.existPseudo) {
      return <Alert variant="warning">Un utilisateur possède le même pseudo</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"ddccc
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter un client</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Nom: </Form.Label>
            <Form.Control
              onChange={this._handleName}
              value={this.state.name}
              type="text"
              placeholder="Le nom du client..."
              aria-label="Nom"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="phoneNumbereeeddd">
            <Form.Label>Pseudo: </Form.Label>
            <Form.Control
              onChange={this._handlePseudo}
              value={this.state.pseudo}
              type="text"
              placeholder="Le pseudo de l'utilisateur..."
              aria-label="Pseudo"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="password">
            <Form.Label>Mot de passe: </Form.Label>
            <PasswordInput onChange={this._handlePassword} value={this.state.password} />
          </Form.Group>
          <Form.Group controlId="level">
            <Form.Label>Rôle: </Form.Label>
            <select onChange={this._handleLevel} className='form-control'>
              <option value={1} selected>Administrateur</option>
              <option value={2}>Manager</option>
              <option value={3}>Caissier/ière</option>
            </select>
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupAddUser;