import React from "react";
import { io } from "socket.io-client";
import { getCommandsNotDone } from "../Services/tef";

export default class VerifyStatus extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      newCommand: false,
    };
    this.nbre = 0;
  }

  componentDidMount() {
    const socket = io("https://api.techeatfast.com", { transports: ["websocket"] });
    socket.on(`2`, (response) => {
      this.setState({ newCommand: true })
    });
  }

  render() {
    if (this.state.newCommand) {
      return (
        <div
          className="blink"
          onClick={() => {
            this.setState({ newCommand: false });
          }}
        >
          <h1>Nouvelle commande</h1>
          <a className="btn btn-danger btn-lg" role="button" href="https://resto.techeatfast.com/commands/live">Allez voir</a>
        </div>
      );
    } else {
      return <span></span>;
    }
  }
}
