import React from 'react';
import { Button, Container, ListGroup, Spinner } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import { getOneSession, getItemOfOneSession, deleteItem, finishSession, finishAndPaySession, paySession, reductionToZero } from '../Services/sessions';
import Cookies from 'js-cookie';
import _ from 'lodash';
import { getOneClient } from '../Services/clients';
import moment from 'moment';
import PopupAddSessionItem from '../Components/PopupAddSessionItem';
import PopupEditSessionItem from '../Components/PopupEditSessionItem';
import PopupAddAccompaniment from '../Components/PopupAddAccompaniment';
import PopupViewAccompaniment from '../Components/PopupViewAccompaniment';
import PopupAddReduction from '../Components/PopupAddReduction';
import PopupEditSession from '../Components/PopupEditSession';
import VerifyStatus from '../Components/VerifyStatus';

class GetOneSession extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sessionInfo: {},
      sessionItem: [],

      isLoading: true,
      addMode: false,
      editMode: false,
      editSessionMode: false,
      accompMode: false,
      accompViewMode: false,
      reductionMode: false,
      error: false,
    };

    this.idSession = this.props.match.params.idSession;
    document.title = "Le Consulat - Sessions";
  }

  async componentDidMount() {
    await getOneSession(this.idSession, Cookies.get('token'))
      .then(async (data) => {
        if (data.find) {
          await getItemOfOneSession(this.idSession, Cookies.get('token'))
            .then(res2 => {
              if (res2.find) {
                this.setState({ isLoading: false, error: false, sessionInfo: data.result[0], sessionItem: res2.result });
              } else {
                this.setState({ isLoading: false, error: true, sessionInfo: [], sessionItem: {} });
              }
            })
            .catch(() => {
              this.setState({ isLoading: false, error: true, sessionInfo: [], sessionItem: {} });
            });
        } else {
          this.setState({ isLoading: false, error: true, sessionInfo: [], sessionItem: {} });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, error: true, sessionInfo: [], sessionItem: {} });
      });
  }

  _updateItem = (info) => {
    this.editInfo = info;
    this.setState({ editMode: true });
  }

  _addAccompaniment = (info) => {
    this.accompInfo = info;
    this.setState({ accompMode: true });
  }

  _viewAccompaniment = (info) => {
    this.accompViewInfo = info;
    this.setState({ accompViewMode: true });
  }

  _deleteItem = (info) => {
    console.log('====================================');
    console.log(info);
    console.log('====================================');
    if (window.confirm('Êtes vous sûre de vouloir supprimer cet élément?')) {
      deleteItem(Cookies.get('token'), info.idSession, info.idSessionsItem)
        .then(res => {
          console.log('====================================');
          console.log(res);
          console.log('====================================');
          if (res.delete) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove('token');
            Cookies.remove('idUser');
      
            window.location.href = '/application/';
          } else {
            alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
          }
        })
        .catch(() => {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        });
    }
  }

  _displayItem = () => {
    if (this.state.sessionItem.length < 1) {
      return <em>Aucun élément</em>;
    } else {
      let arrayElement = [];
      if (this.state.sessionInfo.wasOver === 1) {
        for (let index in this.state.sessionItem) {
          arrayElement.push(
            <ListGroup.Item style={{ fontSize: 13.5 }}>
              <h4 style={{ marginBottom: -1 }}>{this.state.sessionItem[index].nameOfProduct}</h4>
              Quantité: {this.state.sessionItem[index].quantity}<br />
              Prix: {this.state.sessionItem[index].price}$<br />
              Total: {this.state.sessionItem[index].quantity * this.state.sessionItem[index].price}$<br />
              <a href='#' onClick={() => { this._addAccompaniment(this.state.sessionItem[index]) }}>Ajouter un accompagnement</a><br />
              <a href='#' onClick={() => { this._viewAccompaniment(this.state.sessionItem[index]) }}>Voir les accompagnements</a>
            </ListGroup.Item>
          );
        }
      } else {
        for (let index in this.state.sessionItem) {
          arrayElement.push(
            <ListGroup.Item style={{ fontSize: 13.5 }}>
              <h4 style={{ marginBottom: -1 }}>{this.state.sessionItem[index].nameOfProduct} <a style={{ fontSize: 18 }} href='#' onClick={() => { this._updateItem(this.state.sessionItem[index]) }}>Modifier</a> <a style={{ fontSize: 18 }} onClick={() => { this._deleteItem(this.state.sessionItem[index]) }} href='#'>Supprimer</a></h4>
              Quantité: {this.state.sessionItem[index].quantity}<br />
              Prix: {this.state.sessionItem[index].price}$<br />
              Total: {this.state.sessionItem[index].quantity * this.state.sessionItem[index].price}$<br />
              <a href='#' onClick={() => { this._addAccompaniment(this.state.sessionItem[index]) }}>Ajouter un accompagnement</a><br />
              <a href='#' onClick={() => { this._viewAccompaniment(this.state.sessionItem[index]) }}>Voir les accompagnements</a>
            </ListGroup.Item>
          );
        }
      }
      return arrayElement;
    }
  };

  _addItem = () => {
    this.setState({ addMode: true });
  };

  _onClose = () => {
    this.setState({ addMode: false, editMode: false, accompMode: false, accompViewMode: false, reductionMode: false, editSessionMode: false, });
  }

  _finish = () => {
    finishSession(Cookies.get('token'), this.idSession)
      .then(res => {
        if (res.update) {
          window.location.reload();
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        }
      })
      .catch(() => {
        alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
      });
  }

  _finishAndPay = () => {
    finishAndPaySession(Cookies.get('token'), this.idSession)
      .then(res => {
        if (res.update) {
          window.location.reload();
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        }
      })
      .catch(() => {
        alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
      });
  }

  _pay = () => {
    paySession(Cookies.get('token'), this.idSession)
      .then(res => {
        if (res.update) {
          window.location.reload();
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        }
      })
      .catch(() => {
        alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
      });
  }

  _addReduction = () => {
    this.setState({ reductionMode: true });
  };

  _reductionToZero = () => {
    reductionToZero(Cookies.get('token'), this.idSession)
    .then(res => {
      if (res.update) {
        window.location.reload();
      } else if (res.invalidToken) {
        Cookies.remove('token');
        Cookies.remove('idUser');
  
        window.location.href = '/application/';
      } else {
        alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
      }
    })
    .catch(() => {
      alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
    });
  };

  _editSession = () => {
    this.setState({ editSessionMode: true });
  }
  
  _displayButton = () => {
    if (this.state.sessionInfo.wasOver === 1 && this.state.sessionInfo.beenPaid) {
      return null;
    } else if (this.state.sessionInfo.wasOver === 1) {
      return <Button variant="warning" onClick={this._pay}>Payée</Button>
    } else {
      return (
        <>
          <Button variant="danger" onClick={this._finish}>Terminée</Button>{' '}
          <Button variant="warning" onClick={this._finishAndPay}>Terminée et payée</Button>{' '}
          <Button variant="secondary" onClick={this._editSession}>Modifier</Button>{' '}
          <Button variant="primary" onClick={this._addReduction}>{this.state.sessionInfo.reduction >= 0 ? 'Ajouter une réduction' : 'Modifier la réduction'}</Button>
          {' '}{this.state.sessionInfo.reduction > 0 ? <Button variant="info" onClick={this._reductionToZero}>Remettre la réduction à 0</Button> : null}
        </>
      )
    }
  }
  
  _displayButton2 = () => {
    if (this.state.sessionInfo.wasOver === 1) {
      return null;
    } else {
      return (
        <Button variant='success' onClick={this._addItem}><i class="fas fa-plus-circle"></i> Ajouter un élément</Button>
      )
    }
  }

  _displayIframe = () => {
    if (this.state.sessionInfo.wasOver === 1) {
      // return <object data={this.state.sessionInfo.invoiceUrl.replace('http', 'https')} type="application/pdf" width="50%" height="500px"><embed src={this.state.sessionInfo.invoiceUrl.replace('http', 'https')} type="application/pdf" /></object>
      return <a href={this.state.sessionInfo.invoiceUrl.replace('http', 'https')} target="_blank">Voir la facture</a>;
    }
  }

  render() {
    if (this.state.isLoading) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
          </Container>
        </>
      );
    } else if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            <em>Une erreur a eu lieu, réessayez ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      const sessionInfo = this.state.sessionInfo;
      let now = moment.unix(parseInt(sessionInfo.timestamp));
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            {this._displayIframe()}<br />
            {this._displayButton()}
            <h2>Information sur la session</h2>
            <table>
              <tr>
                <td><b>Clients: </b></td>
                <td>{sessionInfo.nameOfClient}</td>
              </tr>
              <tr>
                <td><b>Serveur/Serveuse: </b></td>
                <td>{sessionInfo.nameOfServer}</td>
              </tr>
              <tr>
                <td><b>Utilisateur: </b></td>
                <td>{sessionInfo.nameOfUser}</td>
              </tr>
              <tr>
                <td><b>Date: </b></td>
                <td>{now.format('DD/MM/yyyy [à] HH:mm')}</td>
              </tr>
              <tr>
                <td><b>Total sans réduction: </b></td>
                <td>{sessionInfo.total}$</td>
              </tr>
              <tr>
                <td><b>Réduction: </b></td>
                <td>{sessionInfo.reduction}$</td>
              </tr>
              <tr>
                <td><b>Total: </b></td>
                <td>{sessionInfo.total - sessionInfo.reduction}$</td>
              </tr>
            </table>
            <br />
            <h2>Élement de la session</h2>
            <ListGroup style={{ width: '25rem' }}>
              {this._displayButton2()}
              {this._displayItem()}
            </ListGroup>
            {this.state.addMode ? <PopupAddSessionItem idSession={this.idSession} callback={this._onClose} /> : null}
            {this.state.editMode ? <PopupEditSessionItem data={this.editInfo} callback={this._onClose} /> : null}
            {this.state.accompMode ? <PopupAddAccompaniment data={this.accompInfo} callback={this._onClose} /> : null}
            {this.state.accompViewMode ? <PopupViewAccompaniment data={this.accompViewInfo} callback={this._onClose} /> : null}
            {this.state.reductionMode ? <PopupAddReduction idSession={this.state.sessionInfo.idSession} default={this.state.sessionInfo.reduction} callback={this._onClose} /> : null}
            {this.state.editSessionMode ? <PopupEditSession data={this.state.sessionInfo} callback={this._onClose} /> : null}
            <VerifyStatus />
          </Container>
        </>
      );
    }
  }
}

export default GetOneSession;