import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import Cookies from 'js-cookie';
import { updateOneSessionItem } from '../Services/sessions';
import ProductsList from './ProductsList';

class PopupEditSessionItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      idProduct: this.props.data.idProduct,
      quantity: this.props.data.quantity,

      isLoading: false,
      success: false,
      invalidForm: false,
      negativeStock: false,
      productNotFound: false,
      error: false,
    }
    this.idSession = this.props.data.idSession;
    this.idSessionItem = this.props.data.idSessionsItem;
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleIdProduct = (newProduct) => {
    this.setState({ idProduct: newProduct });
  }

  _handleQuantity = ({ target }) => {
    this.setState({ quantity: target.value });
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });


    const toSend = {
      idProduct: this.state.idProduct,
      quantity: Number(this.state.quantity),
    };

    updateOneSessionItem(toSend, Cookies.get('token'), this.idSession, this.idSessionItem)
      .then(res => {
        if (res.update) {
          this.setState({ isLoading: false, success: true, invalidForm: false, productNotFound: false, negativeStock: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, productNotFound: false, negativeStock: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.productNotFound) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: true, negativeStock: false, error: false });
        } else if (res.negativeStock) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeStock: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeStock: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Élément modifié avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.productNotFound) {
      return <Alert variant="warning">Le produit choisit n'a pas été trouvé</Alert>;
    } else if (this.state.negativeStock) {
      return <Alert variant="warning">Le stock sera négatif si vous faites la transaction</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  _displayButton = () => {
    return !this.state.isLoading ? <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button> : <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>;
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter un article a une session</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="product">
            <Form.Label>Produits: </Form.Label>
            <ProductsList onChange={this._handleIdProduct} default={this.state.idProduct} />
          </Form.Group>
          <Form.Group controlId="quantity">
            <Form.Label>Quantité: </Form.Label>
            <Form.Control
              onChange={this._handleQuantity}
              value={this.state.quantity}
              type="number"
              placeholder="La quantité..."
              aria-label="quantity"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
        {this._displayButton()}
        <Button variant="danger" onClick={this._handleClose}>
          Fermer
        </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupEditSessionItem;