import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import _ from 'underscore';
import Cookies from 'js-cookie';
import CategoriesList from './CategoriesList';
import { addTransaction } from '../Services/guichet';

class PopupAddMoneyTransactions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      idCategory: '',
      type: 'enter',
      amount: 1,
      description: '',

      isLoading: false,
      success: false,
      invalidForm: false,
      negativeMoney: false,
      productNotFound: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleIdCategory = (newProduct) => {
    this.setState({ idCategory: newProduct });
  }

  _handleAmount = (event) => {
    this.setState({ amount: event.target.value });
  }

  _handleType = (event) => {
    this.setState({ type: event.target.value });
  }

  _handleDescription = (event) => {
    this.setState({ description: event.target.value });
  }

  _displayButton = () => {
    if ((this.state.description.length >= 2 && this.state.description.length <= 90 && _.isString(this.state.description)) && _.isNumber(Number(this.state.amount)) && _.isNumber(this.state.idCategory) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button>
    } else {
      return <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = { 
      amount: Number(this.state.amount),
      description: this.state.description,
      idCategory: this.state.idCategory,
    };

    addTransaction(toSend, Cookies.get('token'), this.state.type)
      .then(res => {
        if (res.create) {
          this.setState({ isLoading: false, success: true, invalidForm: false, productNotFound: false, negativeMoney: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, productNotFound: false, negativeMoney: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.productNotFound) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: true, negativeMoney: false, error: false });
        } else if (res.negativeMoney) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeMoney: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeMoney: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeMoney: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Transaction ajouté avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.negativeMoney) {
      return <Alert variant="warning">Le montant sera négatif si vous faites la transaction</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter une entrée</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Catégorie: </Form.Label>
            <CategoriesList onChange={this._handleIdCategory} type={this.state.type} /> 
          </Form.Group>
          <Form.Group controlId="amount">
            <Form.Label>Montant: </Form.Label>
            <Form.Control
              onChange={this._handleAmount}
              value={this.state.amount}
              type="number"
              placeholder="Le montant..."
              aria-label="amount"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="type">
            <Form.Label>Type: </Form.Label>
            <select className="form-control" onChange={this._handleType}>
              <option value="enter" selected>Entrée</option>
              <option value="outlet">Sortie</option>
            </select>
          </Form.Group>
          <Form.Group controlId="description">
            <Form.Label>Libellé: </Form.Label>
            <Form.Control
              onChange={this._handleDescription}
              value={this.state.description}
              type="text"
              placeholder="Libellé de la transaction..."
              aria-label="description"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupAddMoneyTransactions;