import React from 'react';
import MaterialTable from "material-table";

const TableWaiter = (props) => {
  const columns = [
    {
      title: "Serveur",
      field: "name",
    },
    {
      title: "Total monétaire (en $)",
      field: "money",
    },
    {
      title: "Total (en nombre)",
      field: "nbrOfSession",
    },
  ];
  if (props.show) {
    return (
      <MaterialTable
        title='Service serveur(euses)'
        data={props.data}
        columns={columns}
        options={{
          search: true,
          paging: false,
          filtering: true,
          exportButton: true,
        }}
        isLoading={false}
      />
    );
  } else {
    return null;
  }
}

export default TableWaiter;