import Cookies from 'js-cookie';
import { getUserInfo } from '../Services/users';

const verifyIsManager = (callback) => {
  getUserInfo(Cookies.get('idUser'), Cookies.get('token'))
    .then(user => {
      if (user.find) {
        if (user.result.level === 1 || user.result.level === 2) {
          if (callback) {
            callback(user.result);
          }
        } else {
          Cookies.remove('token');
          Cookies.remove('idUser');
      
          window.location.href = '/application/';
        }
      } else {
        alert('Une erreur a eu lieu');
        window.location.href = '/application/';
      }
    })
    .catch(() => {
      alert('Une erreur a eu lieu');
      window.location.href = '/application/';
    });
};

export default verifyIsManager;