import React from 'react';
import { Button, Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import TableCategories from '../Components/TableCategories';
import PopupAddCategories from '../Components/PopupAddCategories';
import PopupEditCategories from '../Components/PopupEditCategories';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';
import PieMPCEnter from '../Components/PieMPCEnter';
import PieMPCOutlet from '../Components/PieMPCOutlet';

class GuichetCategories extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Catégories';

    this.state = {
      addMode: false,
      editMode: false,
    }
    this.infoEdit = null;
  }

  componentDidMount() {
    verifyIsAdmin();
  }

  _addUser = () => {
    this.setState({ addMode: true, editMode: false });
  }

  _onUpdate = (info) => {
    this.infoEdit = info;
    this.setState({ addMode: false, editMode: true })
  }

  _onClose = () => {
    this.setState({ addMode: false, editMode: false });
  }

  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Catégories</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter une catégorie</Button>
          <br /><br />
          <TableCategories onUpdate={this._onUpdate} />
          <br /><br />
          <h2>Catégories d'entrée les plus fréquentes</h2>
          <div style={{ width: '60vh' }}>
            <PieMPCEnter />
          </div>
          <h2>Catégories de sorties les plus fréquentes</h2>
          <div style={{ width: '60vh', alignItems: 'center' }}>
            <PieMPCOutlet />
          </div>
          {this.state.addMode ? <PopupAddCategories callback={this._onClose} /> : null}
          {this.state.editMode ? <PopupEditCategories callback={this._onClose} data={this.infoEdit} /> : null}
        </Container>
      </>
    );
  }
}

export default GuichetCategories;