import { Component } from 'react';
import { Container, Form, Button, Alert } from 'react-bootstrap';
import Logo from '../Components/Logo';
import PasswordInput from '../Components/PasswordInput';
import { login } from '../Services/users';
import Cookies from 'js-cookie';

class Login extends Component {
  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Connexion';
    this.state = {
      pseudo: '',
      password: '',
      alert: '',
      toManyRequest: false,
    };
  }

  componentDidMount() {
    if (Cookies.get('idUser') || Cookies.get('token')) {
      window.location.href = '/application/home';
    }
  };

  _handlePseudo = (event) => {
    this.setState({ pseudo: event.target.value });
  }

  _handlePassword = (event) => {
    this.setState({ password: event.target.value });
  }

  _displayButton = () => {
    return this.state.pseudo.trim().length > 0 && this.state.password.trim().length > 0 ?
      <Button variant="success" id="btnSubmit" type="submit" onClick={this._onClick}>Se connecter</Button>
      :
      <Button variant="success" id="btnSubmit" type="submit" disabled onClick={this._onClick}>Se connecter</Button>
  };

  _onClick = (event) => {
    event.preventDefault();
    const toSend = {
      pseudo: this.state.pseudo,
      password: this.state.password,
    };

    if (!this.state.toManyRequest) {
      login(toSend)
        .then(data => {
          if (data.toManyRequest === true) {
            this.setState({ alert: `Vous avez fait trop de tentative, attendez ${data.minute} minutes`, toManyRequest: true });
          } else if (data.idUser && data.token) {
            Cookies.set('idUser', data.idUser, { expires: 1 });
            Cookies.set('token', data.token, { expires: 1 });
            window.location.href = '/application/home';
          } else if (data.email && !data.password) {
            this.setState({ alert: 'Votre mot de passe est incorrect!', toManyRequest: false });
          } else if (!data.email) {
            this.setState({ alert: 'Votre mot de passe et votre addresse email sont incorrect!', toManyRequest: false });
          }
        })
        .catch(error => {
          this.setState({ alert: 'Une erreur inconnu a eu lieu!' });
        });
    }
  }

  render() {
    return (
      <Container role="main">
        <Logo />
        <h1>Connectez-vous!</h1>
        <Form>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Pseudo: </Form.Label>
            <Form.Control
              type="text"
              placeholder="Votre pseudo..."
              aria-label="Pseudo"
              aria-describedby="btnSubmit"
              onChange={this._handlePseudo}
              value={this.state.pseudo}
            />
          </Form.Group>

          Votre mot de passe:
          <PasswordInput btnId="btnSubmit" value={this.state.password} onChange={this._handlePassword} />
          <Alert variant="danger" show={this.state.alert.length > 0}>
            {this.state.alert}
          </Alert>
          {this._displayButton()}
        </Form>
      </Container>
    );
  };
}

export default Login;