import React from 'react';
import { getAllUser, deleteUser } from '../Services/users';
import Cookies from 'js-cookie';
import MaterialTable from "material-table";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

class TableUsers extends React.Component {
  constructor() {
    super();
    this.state = {
      users: [],
      isLoading: true,
      error: false,
    };

    this.columns = [
      {
        title: "Nom",
        field: "name",
      },
      {
        title: "Pseudo",
        field: "pseudo",
      },
      {
        title: "Rôle",
        field: "level2",
      },
    ];
  }

  componentDidMount() {
    getAllUser(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          let until = [];
          for (let index in data.result) {
            until.push({ ...data.result[index], level2: this._adapt(data.result[index].level) })
          }
          this.setState({ users: until, isLoading: false, error: false });
        } else if (data.invalidToken) {
          Cookies.remove('idUser');
          Cookies.remove('token');
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, error: false });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, error: false });
      });
  }

  _adapt = (toAdapt) => {
    switch (toAdapt) {
      case 3:
        return "Caissier / Caissière";
      case 2:
        return "Manager";
      case 1:
        return "Administrateur";
    }
  }

  _handleDelete = (event, rowData) => {
    if (window.confirm('Êtes vous sûre de vouloir supprimer cette utilisateur')) {
      deleteUser(Cookies.get('token'), rowData.idUser)
        .then(res => {
          if (res.delete) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove('token');
            Cookies.remove('idUser');
      
            window.location.href = '/application/';
          } else if (res.cannotDeleteAdmin) { 
            alert('Vous ne pouvez pas supprimer un administrateur')
          } else {
            alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
          }
        })
        .catch(() => {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        });
    }
  }

  _handleUpdate = (event, rowData) => {
    let capture;
    for (let index in this.state.users) {
      if (this.state.users[index].idUser === rowData.idUser) {
        capture= this.state.users[index];
      }
    }
    this.props.onUpdate(capture);
  }

  render() {
    if (this.state.error) {
      return <h2>Une erreur a eu leiu veuillez réessayer</h2>
    } else {
      return (
        <MaterialTable
          title='Liste des utilisateur'
          data={this.state.users}
          columns={this.columns}
          options={{
            search: true,
            paging: false,
            filtering: true,
            exportButton: true,
          }}
          isLoading={this.state.isLoading}
          actions={[
            {
              icon: () => <EditIcon htmlColor='#007bff' />,
              tooltip: "Modifier",
              onClick: this._handleUpdate,
            },
            {
              icon: () => <DeleteIcon htmlColor='#dc3545' />,
              tooltip: "Supprimer l'utilisateur",
              onClick: this._handleDelete,
            },
          ]}
        />
      );
    }
  }
}

export default TableUsers;