import React from 'react';
import { Button, Container, Spinner } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import moment from 'moment';
import PopupAddMoneyTransactions from '../Components/PopupAddMoneyTransactions';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';
import DatePicker from "react-datepicker"; 
import { getTransactions } from '../Services/guichet';
import Cookies from 'js-cookie';
import _ from 'lodash';
import MaterialTable from 'material-table';

class GuichetTransactions extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Transactions';

    this.state = {
      addMode: false,
      isLoading: true,
      data: null,
      error: false,
      end: null,
      begin: null,
    }
    this.beginInTimestamp= null;
    this.endInTimestamp= null;

    this.columns = [
      {
        title: "Catégorie",
        field: "nameOfCategory",
      },
      {
        title: "Entrée",
        field: "enter",
      },
      {
        title: "Sortie",
        field: "outlet",
      },
      {
        title: "Argent après",
        field: "amountAfter",
      },
      {
        title: "Date",
        field: "date2",
      },
      {
        title: "Libellé",
        field: "description",
      },
    ]
  }

  componentDidMount() {
    verifyIsAdmin();
  }

  _addUser = () => {
    this.setState({ addMode: true });
  }

  _onClose = () => {
    this.setState({ addMode: false });
  }

  _didMount = () => {
    getTransactions(Cookies.get('token'), this.beginInTimestamp, this.endInTimestamp)
      .then((res) => {
        if (res.find) {
          let until = [];
          for (let index in res.result.transaction) {
            let now = moment.unix(parseInt(res.result.transaction[index].timestamp));
            until.push({ ...res.result.transaction[index], date2: now.format('DD/MM/yyyy [à] HH:mm'), });
          }
          this.setState({ error: false, isLoading: false, data: {...res.result, transaction: until} });
        } else {
          this.setState({ error: true, isLoading: false });
        }
      })
      .catch(() => {
        this.setState({ error: true, isLoading: false });
      });
  }

  _handleBegin = (date) => {
    this.setState({ isLoading: true, begin: date }, () => {
      const now = moment(date);
      this.beginInTimestamp= now.unix();
      if (!_.isNull(this.state.end)) {
        this._didMount();
      }
    })
  }

  _handleEnd = (date) => {
    this.setState({ isLoading: true, end: date }, () => {
      const now = moment(date);
      this.endInTimestamp= now.unix();
      if (!_.isNull(this.state.begin)) {
        this._didMount();
      }
    });
  }

  _display = () => {
    if (this.state.isLoading) {
      return (
        <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
      );
    } else if (this.state.error) {
      return <em>Une erreur a eu lieu veuillez réessayez plus tard</em>
    } else {
      return (
        <>
          Montant au début: {this.state.data.begin.length > 0 ? this.state.data.begin[0].amountAfter : 0}$<br />
          Montant à la fin: {this.state.data.end.length > 0 ? this.state.data.end[0].amountAfter : 0}$<br /><br />
          
          Total entrée: {this.state.data.sumEnter.length > 0 ? this.state.data.sumEnter[0].sumEnter : 0}$<br />
          Total sortie: {this.state.data.sumOutlet.length > 0 ? this.state.data.sumOutlet[0].sumOutlet : 0}$<br />
          <br />
          <MaterialTable
            title='Transactions monétaire'
            data={this.state.data.transaction}
            columns={this.columns}
            options={{
              search: true,
              paging: true,
              filtering: true,
              exportButton: true,
            }}
            isLoading={this.state.isLoading}
          />
        </>
      );
    }
  }

  render() {
    console.log(this.state);
    return (
      <>
        <NavBar />
        <Container>
          <h1>Transaction monétaire</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter une transaction</Button> <br /><br />
          Les transactions  du <DatePicker selected={this.state.begin} dateFormat='dd/MM/yyyy' onChange={this._handleBegin} withPortal />
          {' '}au{' '}
          <DatePicker selected={this.state.end} dateFormat='dd/MM/yyyy' onChange={this._handleEnd} withPortal /><br />
          {this._display()}
          {this.state.addMode ? <PopupAddMoneyTransactions callback={this._onClose} /> : null}
        </Container>
      </>
    );
  }
}

export default GuichetTransactions;