import React from 'react';
import { Container, Button } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import SessionsInProgressList from '../Components/SessionsInProgressList';
import PopupAddSession from '../Components/PopupAddSession';
import VerifyStatus from '../Components/VerifyStatus';

class SessionsInProgress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      addMode: false,
    };
    document.title = "Le Consulat - Sessions";
  }

  _addSession = () => {
    this.setState({ addMode: true });
  };

  _onClose = () => {
    this.setState({ addMode: false });
  }
  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Sessions en cours</h1>
          <Button variant='success' onClick={this._addSession}><i class="fas fa-plus-circle"></i> Commencer une nouvelle session</Button>
          <br /><br />
          <SessionsInProgressList />
          <VerifyStatus />
        </Container>
        {this.state.addMode ? <PopupAddSession callback={this._onClose} /> : null}
      </>
    );
  }
}

export default SessionsInProgress;