import React from 'react';
import { Button, Container, Spinner } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import moment from 'moment';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';
import DatePicker from "react-datepicker"; 
import Cookies from 'js-cookie';
import _ from 'lodash';
import MaterialTable from 'material-table';
import PopupAddTransaction from '../Components/PopupAddTransaction';
import { getWholeSale } from '../Services/transactions';

class TransactionWholeSale extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Transactions';

    this.state = {
      addMode: false,
      isLoading: true,
      data: null,
      error: false,
      end: null,
      begin: null,
    }
    this.beginInTimestamp= null;
    this.endInTimestamp= null;

    this.columns = [
      {
        title: "Produit",
        field: "nameOfProduct",
      },
      {
        title: "Stock",
        field: "stock",
      },
      {
        title: "Entrée",
        field: "totalEnter",
      },
      {
        title: "Sortie",
        field: "totalOutlet",
      },
      {
        title: "Solde",
        field: "solde",
      },
    ];
  }

  componentDidMount() {
    verifyIsAdmin();
  }

  _addUser = () => {
    this.setState({ addMode: true });
  }

  _onClose = () => {
    this.setState({ addMode: false });
  }

  _didMount = () => {
    getWholeSale(Cookies.get('token'), this.beginInTimestamp, this.endInTimestamp)
      .then((res) => {
        if (res.find) {
          let until = [];
          for (let index in res.result) {
            until.push({ 
              ...res.result[index], 
              solde: Number(res.result[index].stock) + Number(res.result[index].totalEnter) - Number(res.result[index].totalOutlet)  
            });
          }
          this.setState({ error: false, isLoading: false, data: until });
        } else {
          this.setState({ error: true, isLoading: false });
        }
      })
      .catch(() => {
        this.setState({ error: true, isLoading: false });
      });
  }

  _handleBegin = (date) => {
    this.setState({ isLoading: true, begin: date }, () => {
      const now = moment(date);
      this.beginInTimestamp= now.unix();
      if (!_.isNull(this.state.end)) {
        this._didMount();
      }
    })
  }

  _handleEnd = (date) => {
    this.setState({ isLoading: true, end: date }, () => {
      const now = moment(date);
      this.endInTimestamp= now.unix();
      if (!_.isNull(this.state.begin)) {
        this._didMount();
      }
    });
  }

  _display = () => {
    if (this.state.isLoading) {
      return (
        <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
      );
    } else if (this.state.error) {
      return <em>Une erreur a eu lieu veuillez réessayez plus tard</em>
    } else {
      return (
        <>
          <MaterialTable
            title='Transactions'
            data={this.state.data}
            columns={this.columns}
            options={{
              search: true,
              paging: true,
              filtering: true,
              exportButton: true,
            }}
            isLoading={this.state.isLoading}
          />
        </>
      );
    }
  }

  render() {
    console.log(this.state);
    return (
      <>
        <NavBar />
        <Container>
          <h1>Transactions</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter une transaction</Button> <br /><br />
          Les transactions  du <DatePicker selected={this.state.begin} dateFormat='dd/MM/yyyy' onChange={this._handleBegin} withPortal />
          {' '}au{' '}
          <DatePicker selected={this.state.end} dateFormat='dd/MM/yyyy' onChange={this._handleEnd} withPortal /><br /><br />
          {this._display()}
          {this.state.addMode ? <PopupAddTransaction callback={this._onClose} /> : null}
        </Container>
      </>
    );
  }
}

export default TransactionWholeSale;