import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import _ from 'underscore';
import Cookies from 'js-cookie';
import { updateClient } from '../Services/clients';

class PopupEditClient extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      name: this.props.data.name,
      phoneNumber: this.props.data.phoneNumber,
      email: this.props.data.email,
      birthday: this.props.data.birthday,

      isLoading: false,
      success: false,
      invalidForm: false,
      existName: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleName = (event) => {
    this.setState({ name: event.target.value });
  }

  _handlePhoneNumber = (event) => {
    this.setState({ phoneNumber: event.target.value });
  }

  _handleEmail = (event) => {
    this.setState({ email: event.target.value });
  }
  
  _handleBirthday = (event) => {
    this.setState({ birthday: event.target.value });
  }

  _displayButton = () => {
    if (this.state.name.length >= 2 && this.state.name.length <= 50 && _.isString(this.state.name) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}>Confirmer</Button>
    } else {
      return <Button variant="success" disabled>Confirmer</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = {
      name: this.state.name,
      phoneNumber: this.state.phoneNumber,
      email: this.state.email,
      birthday: this.state.birthday,
    };

    updateClient(Cookies.get('token'), this.props.data.idClient, toSend)
      .then(res => {
        if (res.update) {
          this.setState({ isLoading: false, success: true, invalidForm: false, existName: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, existName: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.existName) {
          this.setState({ isLoading: false, success: false, invalidForm: false, existName: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, existName: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Client modifié avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.existName) {
      return <Alert variant="warning">Un client possède déjà le même nom</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Modifier un client</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Nom: </Form.Label>
            <Form.Control
              onChange={this._handleName}
              value={this.state.name}
              type="text"
              placeholder="Le nom du client..."
              aria-label="Nom"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="phoneNumber">
            <Form.Label>Numéro de téléphone: </Form.Label>
            <Form.Control
              onChange={this._handlePhoneNumber}
              value={this.state.phoneNumber}
              type="text"
              placeholder="Le numéro du client..."
              aria-label="phoneNumber"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="email">
            <Form.Label>Adresse email: </Form.Label>
            <Form.Control
              onChange={this._handleEmail}
              value={this.state.email}
              type="text"
              placeholder="L'email du client..."
              aria-label="email"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="birthday">
            <Form.Label>Date de naissance: </Form.Label>
            <Form.Control
              onChange={this._handleBirthday}
              value={this.state.birthday}
              type="text"
              placeholder="La date de naissance du client..."
              aria-label="naissance"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupEditClient;