import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import _ from 'underscore';
import Cookies from 'js-cookie';
import ProductsList from './ProductsList';
import { addTransaction } from '../Services/transactions';

class PopupAddTransactionManager extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      idProduct: '',
      quantity: 1,
      description: '',

      isLoading: false,
      success: false,
      invalidForm: false,
      negativeStock: false,
      productNotFound: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleIdProduct = (newProduct) => {
    this.setState({ idProduct: newProduct });
  }

  _handleQuantity = (event) => {
    this.setState({ quantity: event.target.value });
  }

  _handleDescription = (event) => {
    this.setState({ description: event.target.value });
  }

  _displayButton = () => {
    if ((this.state.description.length >= 2 && this.state.description.length <= 90 && _.isString(this.state.description)) && _.isNumber(Number(this.state.quantity)) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button>
    } else {
      return <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>
    }
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = { 
      quantity: Number(this.state.quantity),
      description: this.state.description,
    };

    addTransaction(toSend, Cookies.get('token'), "enter", this.state.idProduct)
      .then(res => {
        if (res.create) {
          this.setState({ isLoading: false, success: true, invalidForm: false, productNotFound: false, negativeStock: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, productNotFound: false, negativeStock: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.productNotFound) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: true, negativeStock: false, error: false });
        } else if (res.negativeStock) {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeStock: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeStock: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, productNotFound: false, negativeStock: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Transaction ajouté avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.productNotFound) {
      return <Alert variant="warning">Le produit que vous avez choisi n'existe pas, essayez de recharger la page, ou contactez-nous</Alert>;
    } else if (this.state.negativeStock) {
      return <Alert variant="warning">Le stock sera négatif si vous faites la transaction</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  // _displayBuyPrice = () => {
  //   if (this.state.type === 'enter') {
  //     return (
  //       <Form.Group controlId="buyPrice">
  //         <Form.Label>Prix d'achat (en $): </Form.Label>
  //         <Form.Control
  //           onChange={this._handleBuyPrice}
  //           value={this.state.buyPrice}
  //           type="number"
  //           placeholder="Le prix d'achat du produit..."
  //           aria-label="buyPrice"
  //           aria-describedby="btnSubmit"
  //         />
  //       </Form.Group>
  //     );
  //   }
  // }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter une entrée</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Produits: </Form.Label>
            <ProductsList onChange={this._handleIdProduct} /> 
          </Form.Group>
          {/* {this._displayBuyPrice()} */}
          <Form.Group controlId="quantity">
            <Form.Label>Quantité: </Form.Label>
            <Form.Control
              onChange={this._handleQuantity}
              value={this.state.quantity}
              type="number"
              placeholder="La quantité..."
              aria-label="quantity"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          <Form.Group controlId="description">
            <Form.Label>Libellé: </Form.Label>
            <Form.Control
              onChange={this._handleDescription}
              value={this.state.description}
              type="text"
              placeholder="Libellé de la transaction..."
              aria-label="description"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupAddTransactionManager;