import React from 'react';
import { Button, Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';
import TableProducts from '../Components/TableProducts';
import PopupAddProducts from '../Components/PopupAddProducts';
import PopupEditProducts from '../Components/PopupEditProducts';
import PieMPTypes from '../Components/PieMPTypes';
import PiesMPProducts from '../Components/PiesMPProducts';

class Products extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Produits';

    this.state = {
      addMode: false,
      editMode: false,
    }
    this.infoEdit = null;
  }

  componentDidMount() {
    verifyIsAdmin();
  }

  _addUser = () => {
    this.setState({ addMode: true, editMode: false });
  }

  _onUpdate = (info) => {
    this.infoEdit = info;
    this.setState({ addMode: false, editMode: true })
  }

  _onClose = () => {
    this.setState({ addMode: false, editMode: false });
  }

  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Produits</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter un produit</Button>
          <br /><br />
          <TableProducts onUpdate={this._onUpdate} />
          {this.state.addMode ? <PopupAddProducts callback={this._onClose} /> : null}
          {this.state.editMode ? <PopupEditProducts callback={this._onClose} data={this.infoEdit} /> : null}
          <br />
          <h2>Types de produit les plus vendues: </h2>
          <div style={{ width: '40vh', alignItems: 'center' }}>
            <PieMPTypes />
          </div>
          <h2>Les produits les plus vendues par type: </h2>
          <PiesMPProducts />
          <br /><br />
        </Container>
      </>
    );
  }
}

export default Products;