import url from './url';

export function login(toSend) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: {
      "Content-type": "application/json; charset=UTF-8"
    },
  };
  return fetch(`${url}/users/login`, init)
    .then(res => res.json());
}

export function getUserInfo(idUser, token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/users/${idUser}`, init)
    .then(res => res.json());
}

export function getAllUser(token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/users`, init)
    .then(res => res.json());
}

export function addUser(toSend, token) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/users`, init)
    .then(res => res.json());
}

export function deleteUser(token, idUser) {
  const init = {
    method: "DELETE",
    headers: new Headers({
      'Authorization': 'Bearer '+token,
    })
  };

  return fetch(`${url}/users/${idUser}`, init)
    .then(res => res.json());
} 

export function updateUser(token, idUser, toSend) {
  const init = {
    method: "PUT",
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      "Content-Type": "application/json; charset=UTF-8"
    })
  };

  return fetch(`${url}/users/${idUser}`, init)
    .then(res => res.json());
} 