import React from 'react';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import { getAllClient } from '../Services/clients';
import Select from 'react-select';

class ClientsList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      clients: null, 
      isLoading: true,
    };
  }

  async componentDidMount() {
    let until = [];
    await getAllClient(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          until.push({ value: 'Inconnu', label: "Client inconnu" });
          for (let index in data.result) {
            until.push({ value: data.result[index].idClient, label: data.result[index].name });
          }
          until.push()
          this.setState({ isLoading: false, clients: until });
        } else {
          alert('Une erreur eu lieu, réessayez ou contactez-nous');
        }
      })
      .catch(() => {
        alert('Une erreur eu lieu, réessayez ou contactez-nous');
      });
  }

  _handle = (event) => {
    this.props.onChange(event)
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
    } else {
      if (this.state.clients.length < 1) {
        alert("Vous n'avez aucun produit commencez par en ajouter un");
        window.location.reload();
      } else {
        return this.props.default ? <Select defaultValue={this.props.default} options={this.state.clients} onChange={this._handle}  /> : <Select options={this.state.clients} onChange={this._handle} />;
      }
    }
  }
}

export default ClientsList;