import React from 'react';
import MaterialTable from "material-table";

const TableClientsReport = (props) => {
  const columns = [
    {
      title: "Client",
      field: "nameOfClient",
    },
    {
      title: "Total sans réduction",
      field: "totalWithoutReduction",
    },
    {
      title: "Reduction",
      field: "reduction",
    },
    {
      title: "Total",
      field: "total",
    },
    {
      title: "Facture",
      field: "facture",
    },
  ];
  if (props.show) {
    return (
      <MaterialTable
        title='Clients présents'
        data={props.data}
        columns={columns}
        options={{
          search: true,
          paging: false,
          filtering: true,
          exportButton: true,
        }}
        isLoading={false}
      />
    );
  } else {
    return null;
  }
}

export default TableClientsReport;