import React from 'react';
import { getMostPopularProducts } from '../Services/products';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import { PieChart } from 'react-minimal-pie-chart';

class PiesMPProducts extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      data: null,
      error: false,
    }
  }

  componentDidMount() {
    const color = [
      "#838B8B",
      "#668B8B",
      "#2F4F4F",
      "#528B8B",
    ]
    getMostPopularProducts(Cookies.get('token'))
      .then(res => {
        if (res.find) {
          const untilDrinks = [];
          const untilSpirits = [];
          const untilDishes = [];
          const untilCigarettes = [];

          for (let index in res.result.drinks) {
            untilDrinks.push({ title: res.result.drinks[index].nameOfProduct, value: res.result.drinks[index].value_occurrence, color: color[index] });
          }
          for (let index in res.result.spirits) {
            untilSpirits.push({ title: res.result.spirits[index].nameOfProduct, value: res.result.spirits[index].value_occurrence, color: color[index] });
          }
          for (let index in res.result.dishes) {
            untilDishes.push({ title: res.result.dishes[index].nameOfProduct, value: res.result.dishes[index].value_occurrence, color: color[index] });
          }
          for (let index in res.result.cigarettes) {
            untilCigarettes.push({ title: res.result.cigarettes[index].nameOfProduct, value: res.result.cigarettes[index].value_occurrence, color: color[index] });
          }

          this.setState({ error: false, isLoading: false, data: { drinks: untilDrinks, spirits: untilSpirits, dishes: untilDishes, cigarettes: untilCigarettes, } });
        } else {
          this.setState({ error: true, isLoading: false });
        }
      })
      .catch((err) => {
        console.log('====================================');
        console.log(err);
        console.log('====================================');
        this.setState({ error: true, isLoading: false });
      })
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>;
    } else if (this.state.error) {
      return <em>Une erreur a eu lieu réesayez ou contactez-nous</em>
    } else {
      return (
        <div>
          <h3>Boissons: </h3>
          <div style={{ width: '40vh', alignItems: 'center' }}>
            <PieChart
              data={this.state.data.drinks}
              labelStyle={{
                fill: '#fff',
                opacity: 0.75,
                pointerEvents: 'none',
                fontSize: "25%",
              }}
              labelPosition={50}
              label={({ dataEntry }) => dataEntry.title + " " + Math.round(dataEntry.percentage) + '%'}
            />
          </div>
          <h3>Spiritueux: </h3>
          <div style={{ width: '40vh', alignItems: 'center' }}>
            <PieChart
              data={this.state.data.spirits}
              labelStyle={{
                fill: '#fff',
                opacity: 0.75,
                pointerEvents: 'none',
                fontSize: "25%",
              }}
              labelPosition={50}
              label={({ dataEntry }) => dataEntry.title + " " + Math.round(dataEntry.percentage) + '%'}
            />
          </div>
          <h3>Plâts: </h3>
          <div style={{ width: '40vh', alignItems: 'center' }}>
            <PieChart
              data={this.state.data.dishes}
              labelStyle={{
                fill: '#fff',
                opacity: 0.75,
                pointerEvents: 'none',
                fontSize: "25%",
              }}
              labelPosition={50}
              label={({ dataEntry }) => dataEntry.title + " " + Math.round(dataEntry.percentage) + '%'}
            />
          </div>
          <h3>Cigarettes: </h3>
          <div style={{ width: '40vh', alignItems: 'center' }}>
            <PieChart
              data={this.state.data.cigarettes}
              labelStyle={{
                fill: '#fff',
                opacity: 0.75,
                pointerEvents: 'none',
                fontSize: "25%",
              }}
              labelPosition={50}
              label={({ dataEntry }) => dataEntry.title + " " + Math.round(dataEntry.percentage) + '%'}
            />
          </div>
        </div>
      );
    }
  }
}

export default PiesMPProducts;