import url from './url';

export function getAllClient(token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/clients`, init)
    .then(res => res.json());
}

export function getOneClient(token, idClient) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/clients/${idClient}`, init)
    .then(res => res.json());
}

export function addClient(toSend, token) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/clients`, init)
    .then(res => res.json());
}

export function updateClient(token, idClient, toSend) {
  const init = {
    method: "PUT",
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      "Content-Type": "application/json; charset=UTF-8"
    })
  };

  return fetch(`${url}/clients/${idClient}`, init)
    .then(res => res.json());
} 

export function deleteClient(token, idClient) {
  const init = {
    method: "DELETE",
    headers: new Headers({
      'Authorization': 'Bearer '+token,
    })
  };

  return fetch(`${url}/clients/${idClient}`, init)
    .then(res => res.json());
} 