import React from 'react';
import { getAllProducts } from '../Services/products';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import Select from 'react-select';

class ProductsList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      products: null, 
      isLoading: true,
    };
  }

  async componentDidMount() {
    let until = [];
    await getAllProducts(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          for (let index in data.result) {
            until.push({ value: data.result[index], label: data.result[index].name });
          }
          this.setState({ isLoading: false, products: until }, this.props.default ? () => { } : () => {
            this.props.onChange(this.state.products[0].value.idProduct, this.state.products[0].value);
          });
        } else {
          alert('Une erreur eu lieu, réessayez ou contactez-nous');
        }
      })
      .catch(() => {
        alert('Une erreur eu lieu, réessayez ou contactez-nous');
      });
  }

  _handle = (event) => {
    this.props.onChange(event.value.idProduct, event.value);
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
    } else {
      if (this.state.products.length < 1) {
        alert("Vous n'avez aucun produit commencez par en ajouter un");
        window.location.reload();
      } else {
        return this.props.default ? <Select defaultValue={this.props.default} options={this.state.products} onChange={this._handle}  /> : <Select options={this.state.products} onChange={this._handle} />;
      }
    }
  }
}

export default ProductsList;