import React from 'react';
import { Container } from 'react-bootstrap';
import Table from 'react-bootstrap/Table'
import NavBar from '../Components/NavBar';
import { getAllProducts } from '../Services/products';
import Cookie from 'js-cookie';
import _ from 'lodash';
import moment from 'moment';

class PrintSpirits extends React.Component {
  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Print';
    this.state = {
      info: [],
      toDisplay: [],
      isLoading: true,
      error: false,
      type: "all",
    };
  }

  componentDidMount() {
    getAllProducts(Cookie.get('token'))
      .then(data => {
        if (data.find) {
          this.setState({ info: _.filter(_.orderBy(data.result, ['name'],['asc']), (o) => (o.type === "Spiritueux")), isLoading: false, error: false });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(() => {
        this.setState({ error: true });
      });
  }

  _renderRow = () => {
    let arrayEl = [];
    for (let index in this.state.info) {
      arrayEl.push(
        <tr>
          <td>{this.state.info[index].name}</td>
          <td>{this.state.info[index].inStock}</td>
        </tr>
      );
    }
    window.setTimeout(() => {
      window.print();
    }, 2000);
    return arrayEl;
  }

  render() {
    if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Print</h1>
            <em>Une erreur a eu lieu, réessayez plus tard ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      const now = moment();
      return (
        <Container  style={{ justifyContent: "center", display: "flex", marginTop: 5, fontSize: "300%"}}>
          <table border="1">
            <thead>
              <tr>
                <th colSpan="2" style={{ textAlign: 'center' }}>{now.format('[Le] DD MMM YYYY')}</th>
              </tr>
            </thead>
              {this._renderRow()}
          </table>
        </Container>
      );
    }
  }
}

export default PrintSpirits;
