import React from 'react';
import { getMPCategoriesOutlet } from '../Services/guichet';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import { PieChart } from 'react-minimal-pie-chart';

class PieMPCOutlet extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      data: [],
      error: false,
    }
  }

  componentDidMount() {
    const color = [
      "#838B8B",
      "#668B8B",
      "#2F4F4F",
    ]
    getMPCategoriesOutlet(Cookies.get('token'))
      .then(res => {
        if (res.find) {
          const until = [];
          for (let index in res.result) {
            until.push({ title: res.result[index].nameOfCategory, value: res.result[index].value_occurrence, color: color[index] });
          }
          this.setState({ error: false, isLoading: false, data: until });
        } else {
          this.setState({ error: true, isLoading: false });
        }
      })
      .catch(() => {
        this.setState({ error: true, isLoading: false });
      })
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>;
    } else if (this.state.error) {
      return <em>Une erreur a eu lieu réesayez ou contactez-nous</em>
    } else {
      return (
        <PieChart
          data={this.state.data}
          labelStyle={{
            fill: '#fff',
            opacity: 0.75,
            pointerEvents: 'none',
            fontSize: "25%",
          }}
          labelPosition={50}
          label={({ dataEntry }) => dataEntry.title + " " + Math.round(dataEntry.percentage) + '%'}
        />
      );
    }
  }
}

export default PieMPCOutlet;