import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import Cookies from 'js-cookie';
import _ from 'underscore';
import { addReduction } from '../Services/sessions';

class PopupAddReduction extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      reduction: this.props.default,

      isLoading: false,
      success: false,
      invalidForm: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleReduction = (event) => {
    this.setState({ reduction: event.target.value });
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });


    const toSend = {
      reduction: Number(this.state.reduction)
    }

    addReduction(toSend, Cookies.get('token'), this.props.idSession)
      .then(res => {
        if (res.update) {
          this.setState({ isLoading: false, success: true, invalidForm: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Réduction ajoutée avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  _displayButton = () => {
    if (_.isNumber(Number(this.state.reduction)) && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}><i class="fas fa-plus-circle"></i> Ajouter</Button>;
    } else {
      return <Button variant="success" disabled><i class="fas fa-plus-circle"></i> Ajouter</Button>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Ajouter une réduction</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="reduction">
            <Form.Label>Montant: </Form.Label>
            <Form.Control
              onChange={this._handleReduction}
              value={this.state.reduction}
              type="number"
              placeholder="Le montant de la réduction..."
              aria-label="reduction"
              aria-describedby="btnSubmit"
            />
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupAddReduction;