import React from 'react';
import MaterialTable from "material-table";

const TableProductsSell = (props) => {
  const columns = [
    {
      title: "Nom",
      field: "nameOfProduct",
    },
    {
      title: "Quantité",
      field: "quantity",
    },
    {
      title: "Prix",
      field: "price",
    },
    {
      title: "Total",
      field: "total",
    },
  ];
  if (props.show) {
    return (
      <MaterialTable
        title='Produit vendus'
        data={props.data}
        columns={columns}
        options={{
          search: true,
          paging: false,
          filtering: true,
          exportButton: true,
        }}
        isLoading={false}
      />
    );
  } else {
    return null;
  }
}

export default TableProductsSell;