import React from 'react';
import { Container, Spinner, Accordion, Card, Button } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import DatePicker from "react-datepicker"; 
import _ from 'lodash';
import moment from 'moment';
import { getReportPeriod } from '../Services/sessions';
import Cookies from 'js-cookie';
import Recipe from '../Components/Recipe';
import AllRecipe from '../Components/AllRecipe';
import Expenses from '../Components/Expenses';
import TableProductsSell from '../Components/TableProductsSell';
import TableClientsReport from '../Components/TableClientsReport';
import TableExpensivesTransactions from '../Components/TableExpensivesTransactions';

class SessionsReportPeriod extends React.Component {
  constructor(props) {
    super(props);
    document.title = "Le Consulat - Rapport";
    this.state = {
      info: [],
      end: null,
      begin: null,
      isLoading: true,
      error: false,
    }
    this.beginInTimestamp= null;
    this.endInTimestamp= null;
  }

  _didMount = () => {
    getReportPeriod(Cookies.get('token'), this.beginInTimestamp, this.endInTimestamp)
      .then(data => {
				if (data.find) {
					let until = [];
					let until2 = [];
					for (let index in data.productsSell) {
						until.push({
							...data.productsSell[index],
							total: `${data.productsSell[index].price * data.productsSell[index].quantity}$`,
							price: `${data.productsSell[index].price}$`,
						});
					}
					for (let index in data.sessions) {
						until2.push({
							...data.sessions[index],
							total: `${data.sessions[index].total - data.sessions[index].reduction}$`,
							reduction: `${data.sessions[index].reduction}$`,
							facture: !_.isNull(data.sessions[index].invoiceUrl) ? <a href={data.sessions[index].invoiceUrl} target="_blank">Voir <i className="fas fa-external-link-alt"></i></a> : 'Aucune facture disponible',
							totalWithoutReduction: `${data.sessions[index].total}$`,
						});
					}
					this.setState({ error: false, isLoading: false, info: { ...data, productsSell: until, sessions: until2, } });

				} else {
					this.setState({ error: true });
				}
      })
      .catch(() => {
        this.setState({ error: true });
      })
  };

  _handleBegin = (date) => {
    this.setState({ isLoading: true, begin: date }, () => {
      const now = moment(date);
      this.beginInTimestamp= now.unix();
      if (!_.isNull(this.state.end)) {
        this._didMount();
      }
    })
  }

  _handleEnd = (date) => {
    this.setState({ isLoading: true, end: date }, () => {
      const now = moment(date);
      this.endInTimestamp= now.unix();
      if (!_.isNull(this.state.begin)) {
        this._didMount();
      }
    });
  }

  _displaySpinner = () => {
		return this.state.isLoading ? <div style={{ textAlign: 'center' }}><Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner></div> : null;
  }

  render() {
		if (this.state.error) {
			return (
				<>
					<NavBar />
					<Container>
						<h1>Rapport d'une période</h1>
						Sélectionnez une période: du <DatePicker selected={this.state.begin} dateFormat='dd/MM/yyyy' onChange={this._handleBegin} withPortal />
            {' '}au{' '}
            <DatePicker selected={this.state.end} dateFormat='dd/MM/yyyy' onChange={this._handleEnd} withPortal /><br />
						<hr style={{ backgroundColor: 'black', height: 1.5, width: '85%' }} />
						Une erreur eu lieu, réessayez ou contactez-nous
					</Container>
				</>
			);
		} else {
			return (
				<>
					<NavBar />
					<Container>
						<h1>Rapport d'une période</h1>
						Sélectionnez une période: du <DatePicker selected={this.state.begin} dateFormat='dd/MM/yyyy' onChange={this._handleBegin} withPortal />
            {' '}au{' '}
            <DatePicker selected={this.state.end} dateFormat='dd/MM/yyyy' onChange={this._handleEnd} withPortal /><br />
						<hr style={{ backgroundColor: 'black', height: 1.5, width: '85%' }} />
						{this._displaySpinner()}
						<Recipe show={!this.state.isLoading} recipe={this.state.info.recipe} />
						<AllRecipe show={!this.state.isLoading} recipe={this.state.info.allRecipe} />
						<Expenses show={!this.state.isLoading} recipe={this.state.info.expenses} />
						<Accordion>
							<Card>
								<Card.Header>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										<h4 style={{ display: 'inline' }}>Produits vendus</h4>
										<Accordion.Toggle as={Button} variant="primary" eventKey="0">
											Ouvrir&nbsp;&nbsp;<i class="fas fa-chevron-down"></i>
										</Accordion.Toggle>
									</div>
								</Card.Header>
								<Accordion.Collapse eventKey="0">
									<TableProductsSell show={!this.state.isLoading} data={this.state.info.productsSell} />
								</Accordion.Collapse>
							</Card>
							<Card>
								<Card.Header>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										<h4 style={{ display: 'inline' }}>Sessions</h4>
										<Accordion.Toggle as={Button} variant="primary" eventKey="1">
											Ouvrir&nbsp;&nbsp;<i class="fas fa-chevron-down"></i>
										</Accordion.Toggle>
									</div>
								</Card.Header>
								<Accordion.Collapse eventKey="1">
									<TableClientsReport show={!this.state.isLoading} data={this.state.info.sessions} />
								</Accordion.Collapse>
							</Card>
							<Card>
								<Card.Header>
									<div style={{ display: 'flex', justifyContent: 'space-between' }}>
										<h4 style={{ display: 'inline' }}>Transactions de dépense</h4>
										<Accordion.Toggle as={Button} variant="primary" eventKey="2">
											Ouvrir&nbsp;&nbsp;<i class="fas fa-chevron-down"></i>
										</Accordion.Toggle>
									</div>
								</Card.Header>
								<Accordion.Collapse eventKey="2">
									<TableExpensivesTransactions show={!this.state.isLoading} data={this.state.info.expensesTransactions} />
								</Accordion.Collapse>
							</Card>
						</Accordion>
					</Container>
				</>
			);
		}
  }
}

export default SessionsReportPeriod;