import React from 'react';
import { Button, Modal, Spinner } from 'react-bootstrap';
import Cookies from 'js-cookie';
import { getAccompOfItem } from '../Services/sessions';
import { getOneProduct } from '../Services/products';

class PopupViewAccompaniment extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      accompaniments: [],

      isLoading: false,
      error: false,
    }
    this.idSessionItem = this.props.data.idSessionsItem;
    this.idSession = this.props.data.idSession;
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  async componentDidMount() {
    var until = [];
    getAccompOfItem(this.idSession, Cookies.get('token'), this.idSessionItem)
      .then(async data => {
        if (data.find) {
          for (let index in data.result) {
            await getOneProduct(Cookies.get('token'), data.result[index].idProduct)
              .then(res => {
                if (res.find) {
                  until.push({ ...data.result[index], productInfo: res.result });
                } else {
                  this.setState({ error: true, isLoading: false });
                }
              })
              .catch(() => {
                this.setState({ error: true, isLoading: false });
              });
          }
          this.setState({ accompaniments: until, isLoading: false, error: false });
        } else if (data.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else {
          this.setState({ error: true, isLoading: false });
        }
      })
      .catch(() => {
        this.setState({ error: true, isLoading: false });
      });
  }

  _display = () => {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>;
    } else if (this.state.error) {
      return <em>Une erreur a eu lieu, réessayez ou contactez-nous</em>;
    } else {
      if (this.state.accompaniments.length < 1) {
        return <em>Aucun accompagnement</em>;
      } else {
        let arrayElement = [];
        for (let index in this.state.accompaniments) {
          arrayElement.push(
            <li>
              <b>Nom</b>: {this.state.accompaniments[index].productInfo.name}<br />
              <b>Quantité</b>: {this.state.accompaniments[index].quantity}<br />
            </li>
          );
        }
        return (<ol>{arrayElement} </ol>);
      }
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Accompagnement</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          {this._display()}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupViewAccompaniment;