import React from 'react';
import { Button, Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import TableClients from '../Components/TableClients';
import PopupAddClient from '../Components/PopupAddClient';
import PopupEditClient from '../Components/PopupEditClient';

class Clients extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Clients';

    this.state = {
      addMode: false,
      editMode: false,
    }
    this.infoEdit = null;
  }

  _addUser = () => {
    this.setState({ addMode: true, editMode: false });
  }

  _onUpdate = (info) => {
    this.infoEdit = info;
    this.setState({ addMode: false, editMode: true })
  }

  _onClose = () => {
    this.setState({ addMode: false, editMode: false });
  }

  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Clients</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter un client</Button>
          <br /><br />
          <TableClients onUpdate={this._onUpdate} />
          {this.state.addMode ? <PopupAddClient callback={this._onClose} /> : null}
          {this.state.editMode ? <PopupEditClient callback={this._onClose} data={this.infoEdit} /> : null}
        </Container>
      </>
    );
  }
}

export default Clients;