import React from 'react';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import { getUserInfo } from '../Services/users';
import AdminNavBar from './AdminNavBar';
import MediumNavBar from './MediumNavBar';
import ManagerNavBar from './ManagerNavBar';

class NavBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dataUser: null,
      isLoading: true,
    };

    if (!Cookies.get("idUser") || !Cookies.get("token")) {
      window.location.href = "/application/";
    }
  }

  componentDidMount() {
    getUserInfo(Cookies.get("idUser"), Cookies.get("token"))
      .then((data) => {
        if (data.find) {
          this.setState({ dataUser: data.result, isLoading: false });
        } else {
          window.alert("Une erreur inconnue a eu lieu");
        }
      })
      .catch(() => {
        window.alert("Une erreur inconnue a eu lieu");
      });
  }

  render() {
    if (this.state.isLoading) {
      return (
        <Spinner animation='border' role='status'>
          <span className='sr-only'>Loading...</span>
        </Spinner>
      );
    } else {
      if (this.state.dataUser.level === 1) {
        return <AdminNavBar userInfo={this.state.dataUser} />;
      } else if (this.state.dataUser.level === 2) {
        return <ManagerNavBar userInfo={this.state.dataUser} />;
      } else if (this.state.dataUser.level === 3) {
        return <MediumNavBar userInfo={this.state.dataUser} />;
      }
    }
  }
}

NavBar.defaultProps = {
  redirect: false
};

export default NavBar;