import React from 'react';
import Cookies from 'js-cookie';
import MaterialTable from "material-table";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { deleteProducts, getAllProducts } from '../Services/products';

class TableProducts extends React.Component {
  constructor() {
    super();
    this.state = {
      products: [],
      isLoading: true,
      error: false,
    };

    this.columns = [
      {
        title: "Nom",
        field: "name",
      },
      {
        title: "Type",
        field: "type",
      },
      {
        title: "Prix",
        field: "price",
      },
      {
        title: "Prix d'achat",
        field: "buyPrice",
      },
      {
        title: "Stock",
        field: "inStock",
      },
      {
        title: "Unité",
        field: "unit",
      },
      {
        title: "Valeur",
        field: "value",
      },
    ];
  }

  async componentDidMount() {
    var until = [];
    await getAllProducts(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          for (let index in data.result) {
            until.push({ ...data.result[index], value: `${data.result[index].price * data.result[index].inStock}$`, price: `${data.result[index].price}$` });
          }
          this.setState({ products: until, isLoading: false, error: false });
        } else if (data.invalidToken) {
          Cookies.remove('idUser');
          Cookies.remove('token');
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, error: false });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, error: false });
      });
  }

  _adapt = (info) => {
    return info === 1 ? 'Oui' : 'Non';
  }

  _handleUpdate = (event, rowData) => {
    let capture;
    for (let index in this.state.products) {
      if (this.state.products[index].idProduct === rowData.idProduct) {
        capture= this.state.products[index];
      }
    }
    this.props.onUpdate(capture);
  }

  _handleDelete = (event, rowData) => {
    if (window.confirm('Êtes vous sûre de vouloir supprimer ce produit?')) {
      deleteProducts(Cookies.get('token'), rowData.idProduct)
        .then(res => {
          if (res.delete) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove('token');
            Cookies.remove('idUser');
      
            window.location.href = '/application/';
          } else {
            alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
          }
        })
        .catch(() => {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        });
    }
  }

  render() {
    if (this.state.error) {
      return <h2>Une erreur a eu leiu veuillez réessayer</h2>
    } else {
      return (
        <MaterialTable
          title='Liste des produits'
          data={this.state.products}
          columns={this.columns}
          options={{
            search: true,
            paging: false,
            filtering: true,
            exportButton: true,
          }}
          isLoading={this.state.isLoading}
          actions={[
            {
              icon: () => <EditIcon htmlColor='#007bff' />,
              tooltip: "Modifier",
              onClick: this._handleUpdate,
            },
            {
              icon: () => <DeleteIcon htmlColor='#dc3545' />,
              tooltip: "Supprimer le produit",
              onClick: this._handleDelete,
            },
          ]}
        />
      );
    }
  }
}

export default TableProducts;