import url from './url';

export function getAllProducts(token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/products`, init)
    .then(res => res.json());
}

export function getOneProduct(token, idProduct) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/products/${idProduct}`, init)
    .then(res => res.json());
}

