export function addProducts(toSend, token) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/products`, init)
    .then(res => res.json());
}

export function updateProducts(token, idProducts, toSend) {
  const init = {
    method: "PUT",
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      "Content-Type": "application/json; charset=UTF-8"
    })
  };

  return fetch(`${url}/products/${idProducts}`, init)
    .then(res => res.json());
} 

export function deleteProducts(token, idProducts) {
  const init = {
    method: "DELETE",
    headers: new Headers({
      'Authorization': 'Bearer '+token,
    })
  };

  return fetch(`${url}/products/${idProducts}`, init)
    .then(res => res.json());
} 