import React from 'react';
import { Spinner, Card, Button } from "react-bootstrap";
import { getInProgress } from '../Services/sessions';
import Cookies from 'js-cookie';
import _ from 'lodash';
import { getOneClient } from '../Services/clients';
import moment from 'moment';

class SessionsInProgressList extends React.Component {
  constructor() {
    super();
    this.state = {
      sessions: [],
      isLoading: true,
      error: false,
    };
  }

  async componentDidMount() {
    var until= []
    await getInProgress(Cookies.get('token'))
      .then(async data => {
        if (data.find) {
          this.setState({ sessions: data.result, isLoading: false, error: false });
        } else {
          this.setState({ sessions: [], isLoading: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ sessions: [], isLoading: false, error: true });
      });
  }

  _displayCards = () => {
    if (this.state.sessions.length < 1) {
      return <em>Aucune session en cours</em>;
    } else {
      let arrayElement = [];
      for (let index in this.state.sessions) {
        let now = moment.unix(parseInt(this.state.sessions[index].timestamp));
        arrayElement.push(
          <Card className="col-12 col-lg-3">
            <Card.Body>
              <Card.Title>{this.state.sessions[index].nameOfClient}</Card.Title>
              <Card.Text>
                Utilisateur: {this.state.sessions[index].nameOfUser}<br />
                  Date: {now.format('DD/MM/yyyy [à] HH:mm')}<br />
                  Total sans réduction: {this.state.sessions[index].total}$<br />
                  Reduction: {this.state.sessions[index].reduction}$<br />
                  Total: {this.state.sessions[index].total - this.state.sessions[index].reduction}$<br />
              </Card.Text>
              <Button variant="primary" onClick={() => { window.location.href = `/application/sessions/get/${this.state.sessions[index].idSession}`; }}>Voir plus <i class="fas fa-arrow-right"></i></Button>
            </Card.Body>
          </Card>
        );
      }
      return arrayElement;
    }
  };

  _display = () => {
    if (this.state.isLoading) {
      return (
        <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
      );
    } else if (this.state.error) {
      return <em>Nous sommes désolé mais une erreur a eu lieu, réessayez ou contactez-nous</em>;
    } else {
      return <div className='row'>
        {this._displayCards()}
      </div>;
    }
  }

  render() {
    return (
      <>
        {this._display()}
      </>
    );
  }
}

export default SessionsInProgressList;