import React from 'react';
import { getAllCategories } from '../Services/guichet';
import Cookies from 'js-cookie';
import { Spinner } from 'react-bootstrap';
import Select from 'react-select';
import _ from 'lodash';

class CategoriesList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      categories: [], 
      categoriesToShow: [], 
      isLoading: true,
    };
  }

  async componentDidMount() {
    let until = [];
    await getAllCategories(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          for (let index in data.result) {
            until.push({ value: data.result[index].idCategory, label: data.result[index].name, type: data.result[index].type });
          }
          this.setState({ isLoading: false, categories: until }, () => {
            const until = _.filter(this.state.categories, (o) =>  o.type === this.props.type);
            this.setState({ categoriesToShow: until });
          });
        } else {
          alert('Une erreur eu lieu, réessayez ou contactez-nous');
        }
      })
      .catch(() => {
        alert('Une erreur eu lieu, réessayez ou contactez-nous');
      });
  }

  _handle = (event) => {
    this.props.onChange(event.value);
  }

  componentDidUpdate(prevProps) {
    if (this.props.type !== prevProps.type) {
      const until = _.filter(this.state.categories, (o) =>  o.type === this.props.type);
      this.setState({ categoriesToShow: until });
    }
  }

  render() {
    if (this.state.isLoading) {
      return <Spinner animation="border" role="status"><span className="sr-only">Loading...</span></Spinner>
    } else {
      if (this.state.categories.length < 1) {
        alert("Vous n'avez aucune catégorie commencez par en ajouter un");
        window.location.reload();
      } else {
        return this.props.default ? <Select defaultValue={this.props.default} options={this.state.categoriesToShow} onChange={this._handle}  /> : <Select options={this.state.categoriesToShow} onChange={this._handle} />;
      }
    }
  }
}

export default CategoriesList;