import React from 'react';
import MaterialTable from "material-table";

const TableExpensivesTransactions = (props) => {
  const columns = [
    {
      title: "Categorie",
      field: "nameOfCategory",
    },
    {
      title: "Montant",
      field: "outlet",
    },
    {
      title: "Libellé",
      field: "description",
    },
  ];
  if (props.show) {
    return (
      <MaterialTable
        title='Transactions de dépense'
        data={props.data}
        columns={columns}
        options={{
          search: true,
          paging: false,
          filtering: true,
          exportButton: true,
        }}
        isLoading={false}
      />
    );
  } else {
    return null;
  }
}

export default TableExpensivesTransactions;