import React from 'react';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import Cookies from 'js-cookie';
import ClientsList from './ClientsList';
import { editSession } from '../Services/sessions';

class PopupEditSession extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      idClient: this.props.data.idClient,
      server: 'CHRISTELLE',

      isLoading: false,
      success: false,
      invalidForm: false,
      negativeStock: false,
      clientNotFound: false,
      error: false,
    }
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  }

  _handleIdClient = (newClient) => {
    this.setState({ idClient: newClient.value });
  }

  _handleServer = ({ target }) => {
    this.setState({ server: target.value });
  }

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });


    const toSend = this.state.idClient === 'Inconnu' ? {
      nameOfServer: this.state.server,
    } : {
      idClient: this.state.idClient,
      nameOfServer: this.state.server,
    };

    editSession(toSend, Cookies.get('token'), this.props.data.idSession)
      .then(res => {
        if (res.update) {
          this.setState({ isLoading: false, success: true, invalidForm: false, clientNotFound: false, negativeStock: false, error: false });
          setTimeout(() => {
            window.location.reload();
          }, 750);
        } else if (res.invalidForm) {
          this.setState({ isLoading: false, success: false, invalidForm: true, clientNotFound: false, negativeStock: false, error: false });
        } else if (res.invalidToken) {
          Cookies.remove('token');
          Cookies.remove('idUser');
    
          window.location.href = '/application/';
        } else if (res.clientNotFound) {
          this.setState({ isLoading: false, success: false, invalidForm: false, clientNotFound: true, negativeStock: false, error: false });
        } else if (res.negativeStock) {
          this.setState({ isLoading: false, success: false, invalidForm: false, clientNotFound: false, negativeStock: true, error: false });
        } else {
          this.setState({ isLoading: false, success: false, invalidForm: false, clientNotFound: false, negativeStock: false, error: true });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, success: false, invalidForm: false, clientNotFound: false, negativeStock: false, error: true });
      });
  }

  _displayAlert = () => {
    if (this.state.success) {
      return <Alert variant="success">Session modifiée avec succès</Alert>;
    } else if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.clientNotFound) {
      return <Alert variant="warning">Le client choisit n'a pas été trouvé</Alert>;
    } else if (this.state.negativeStock) {
      return <Alert variant="warning">Le stock sera négatif si vous faites la transaction</Alert>;
    } else if (this.state.error) {
      return <Alert variant="danger">Une erreur a eu lieu, réessayer ou contactez-nous</Alert>;
    }
  }

  _displayButton = () => {
    if (this.state.server.length > 0 && this.state.server.length <= 75 && !this.state.isLoading) {
      return <Button variant="success" onClick={this._onClick}>Confirmer</Button>;
    } else {
      return <Button variant="success" disabled>Confirmer</Button>;
    }
  }

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Modfier une session</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Group controlId="name">
            <Form.Label>Client: </Form.Label>
            <ClientsList onChange={this._handleIdClient} default={this.props.data.idClient} />
          </Form.Group>
          <Form.Group controlId="nameOfServer">
            <Form.Label>Nom du serveur/serveuse: </Form.Label>
            <select onChange={this._handleServer} id="nameOfServer" className="form-control">
              <option value='CHRISTELLE' selected>CHRISTELLE</option>
              <option value='NDAYA CHRISTELLE'>NDAYA CHRISTELLE</option>
              <option value='EXAUCÉE'>EXAUCÉE</option>
              <option value='SANDRA'>SANDRA</option>
              <option value='FANNY'>FANNY</option>
              <option value='PATIENT'>PATIENT</option>
              <option value='GRACIA'>GRACIA</option>
            </select>
          </Form.Group>
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
  );
  }
}

export default PopupEditSession;