import React from 'react';
import { Container } from 'react-bootstrap';
import Table from 'react-bootstrap/Table'
import NavBar from '../Components/NavBar';
import { getAllProducts } from '../Services/products';
import Cookie from 'js-cookie';
import _ from 'lodash';
import moment from 'moment';

class PrintAll extends React.Component {
  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Print';
    this.state = {
      info: [],
      toDisplay: [],
      isLoading: true,
      error: false,
      type: "all",
    };
    this.nbre = 0;
  }

  componentDidMount() {
    getAllProducts(Cookie.get('token'))
      .then(data => {
        if (data.find) {
          this.setState({ info: _.orderBy(_.orderBy(data.result, ['name'],['asc']), ['type'],['asc']), isLoading: false, error: false });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(() => {
        this.setState({ error: true });
      });
  }

  _renderRow = () => {
    if (this.state.info.length > 0) {
      let arrayEl = [];
      let lastType = this.state.info[0].type;
      arrayEl.push(
        <tr>
          <td colSpan="2" style={{ textAlign: "center" }}>
            {lastType}
          </td>
        </tr>
      );
      for (let index in this.state.info) {
        if (lastType !== this.state.info[index].type) {
          arrayEl.push(
            <tr>
              <td colSpan="2" style={{ textAlign: "center" }}>
                {this.state.info[index].type}
              </td>
            </tr>
          );
          arrayEl.push(
            <tr>
              <td>{this.state.info[index].name}</td>
              <td>{this.state.info[index].inStock}</td>
            </tr>
          );
          lastType = this.state.info[index].type;
        } else {
          arrayEl.push(
            <tr>
              <td>{this.state.info[index].name}</td>
              <td>{this.state.info[index].inStock}</td>
            </tr>
          );
        }
      }
      window.setTimeout(() => {
        window.print();
      }, 2000);
      return arrayEl;
    }
  }

  render() {
    if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Print</h1>
            <em>Une erreur a eu lieu, réessayez plus tard ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      const now = moment();
      return (
        <Container  style={{ justifyContent: "center", display: "flex", marginTop: 5, fontSize: "300%"}}>
          <table border="1">
            <thead>
              <tr>
                <th colSpan="2" style={{ textAlign: 'center' }}>{now.format('[Le] DD MMM YYYY')}</th>
              </tr>
            </thead>
            {this._renderRow()}
          </table>
        </Container>
      );
    }
  }
}

export default PrintAll;
