import url from './url';

export function getAllCategories(token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/money-transactions/category`, init)
    .then(res => res.json());
}

export function addCategory(toSend, token) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/money-transactions/category`, init)
    .then(res => res.json());
}

export function updateCategory(toSend, idCategory, token) {
  const init = {
    method: "PUT",
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      "Content-Type": "application/json; charset=UTF-8"
    })
  };

  return fetch(`${url}/money-transactions/category/${idCategory}`, init)
    .then(res => res.json());
} 

export function addTransaction(toSend, token, type) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  if (type === 'enter') {
    return fetch(`${url}/money-transactions/enter`, init)
      .then(res => res.json());
  } else if (type === 'outlet') {
    return fetch(`${url}/money-transactions/outlet`, init)
      .then(res => res.json());
  }
}

export function getTransactions(token, begin, end) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/money-transactions/transactions/begin/${begin}/end/${end}`, init)
    .then(res => res.json());
}

