import url from './url';

export function getInProgress(token) {
  const init = {
    method: "GET",
    headers: new Headers({
      'Authorization': 'Bearer '+token, 
      'Content-Type': 'application/x-www-form-urlencoded'
    })
  };

  return fetch(`${url}/sessions/get/notFinished`, init)
    .then(res => res.json());
}

