export function startSession(toSend, token) {
  const init = {
    method: 'POST',
    body: JSON.stringify(toSend),
    headers: new Headers({
      'Authorization': 'Bearer ' + token,
      "Content-type": "application/json; charset=UTF-8"
    })
  };
  return fetch(`${url}/sessions`, init)
    .then(res => res.json());
}

