import React from 'react';
import Cookies from 'js-cookie';
import MaterialTable from "material-table";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { getAllClient, deleteClient } from '../Services/clients';

class TableClients extends React.Component {
  constructor() {
    super();
    this.state = {
      clients: [],
      isLoading: true,
      error: false,
    };

    this.columns = [
      {
        title: "Nom",
        field: "name",
      },
      {
        title: "Numéro de téléphone",
        field: "phoneNumber",
      },
      {
        title: "Adresse email",
        field: "email",
      },
      {
        title: "Anniversaire",
        field: "birthday",
      },
    ];
  }

  componentDidMount() {
    getAllClient(Cookies.get('token'))
      .then(data => {
        if (data.find) {
          let until = [];
          for (let index in data.result) {
            until.push({ ...data.result[index] })
          }
          this.setState({ clients: until, isLoading: false, error: false });
        } else if (data.invalidToken) {
          Cookies.remove('idUser');
          Cookies.remove('token');
          window.location.href = '/application/';
        } else {
          this.setState({ isLoading: false, error: false });
        }
      })
      .catch(() => {
        this.setState({ isLoading: false, error: false });
      });
  }

  _handleUpdate = (event, rowData) => {
    let capture;
    for (let index in this.state.clients) {
      if (this.state.clients[index].idClient === rowData.idClient) {
        capture= this.state.clients[index];
      }
    }
    this.props.onUpdate(capture);
  }

  _handleDelete = (event, rowData) => {
    if (window.confirm('Êtes vous sûre de vouloir supprimer ce client?')) {
      deleteClient(Cookies.get('token'), rowData.idClient)
        .then(res => {
          if (res.delete) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove('token');
            Cookies.remove('idUser');
      
            window.location.href = '/application/';
          } else {
            alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
          }
        })
        .catch(() => {
          alert('Une erreur a eu lieu lors de l\'opération, réessayez ou contactez nous');
        });
    }
  }

  render() {
    if (this.state.error) {
      return <h2>Une erreur a eu leiu veuillez réessayer</h2>
    } else {
      return (
        <MaterialTable
          title='Liste des clients'
          data={this.state.clients}
          columns={this.columns}
          options={{
            search: true,
            paging: false,
            filtering: true,
            exportButton: true,
          }}
          isLoading={this.state.isLoading}
          actions={[
            {
              icon: () => <EditIcon htmlColor='#007bff' />,
              tooltip: "Modifier",
              onClick: this._handleUpdate,
            },
            {
              icon: () => <DeleteIcon htmlColor='#dc3545' />,
              tooltip: "Supprimer le client",
              onClick: this._handleDelete,
            },
          ]}
        />
      );
    }
  }
}

export default TableClients;