import React from 'react';
import moment from 'moment';
import { Container, Button } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';
import DatePicker from "react-datepicker"; 
import "react-datepicker/dist/react-datepicker.css";
import { getAllTransactions } from '../Services/transactions';
import Cookies from 'js-cookie';
import _ from "lodash";
import MaterialTable from "material-table";
import PopupAddTransaction from '../Components/PopupAddTransaction';

class TransactionDetail extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      begin: null,
      beginInTimestamp: null,
      end: null,
      endInTimestamp: null,

      addMode: false,

      transaction: [],
      transactionToFilter: [],
      isLoading: true,
      error: false,
    };
    document.title = "Consulat - Transactions";
    this.columns = [
      {
        title: "Produit",
        field: "nameOfProduct",
      },
      {
        title: "Utilisateur",
        field: "nameOfUser",
      },
      {
        title: "Stock",
        field: "stock",
      },
      {
        title: "Entrée",
        field: "enter",
      },
      {
        title: "Sortie",
        field: "outlet",
      },
      {
        title: "Solde",
        field: "stockAfter",
      },
      {
        title: "Libellé",
        field: "description",
      },
      {
        title: "Date",
        field: "date2",
      },
    ];
  }

  componentDidMount() {
    verifyIsAdmin(() => {
      getAllTransactions(Cookies.get('token'))
        .then(data => {
          if (data.find) {
            let until = [];
            for (let index in data.result) {
              let now = moment.unix(parseInt(data.result[index].timestamp));
              until.push({ ...data.result[index], date2: now.format('DD/MM/yyyy [à] HH:mm'), stock: data.result[index].stockAfter - data.result[index].enter + data.result[index].outlet });
            }
            this.setState({ transaction: until, isLoading: false, error: false });
          } else if (data.invalidToken) {
            Cookies.remove('token');
            Cookies.remove('idUser');
      
            window.location.href = '/application/';
          } else {
            this.setState({ transaction: [], isLoading: false, error: true });
          }
        })
        .catch(() => {
          this.setState({ transaction: [], isLoading: false, error: true });
        });
    });
  }

  _handleBegin = (date) => {
    const now = moment(date);
    this.setState({ begin: date, beginInTimestamp: now.unix() }, () => {
      const until = _.filter(this.state.transaction, (o) =>  o.timestamp >= this.state.beginInTimestamp && o.timestamp <= this.state.endInTimestamp);
      this.setState({ transactionToFilter: until });
    });
  }

  _handleEnd = (date) => {
    const now = moment(date);
    this.setState({ end: date, endInTimestamp: now.unix() }, () => {
      const until = _.filter(this.state.transaction, (o) =>  (o.timestamp >= this.state.beginInTimestamp) && (o.timestamp <= this.state.endInTimestamp));
      this.setState({ transactionToFilter: until });
    });
  }

  _display = () => {
    if (this.state.error) {
      return 'Une erreur a eu lieu, réessayez ou contactez-nous';
    } else {
      return (
        <MaterialTable
          title='Transaction'
          data={this.state.transactionToFilter}
          columns={this.columns}
          options={{
            search: true,
            paging: true,
            filtering: true,
            exportButton: true,
          }}
          isLoading={this.state.isLoading}
        />
      );
    }
  }

  _addTransaction = () => {
    this.setState({ addMode: true });
  };

  _onClose = () => {
    this.setState({ addMode: false });
  }

  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Transactions</h1>
          <Button variant='success' onClick={this._addTransaction}><i class="fas fa-plus-circle"></i> Ajouter une transaction</Button> <br /><br />
          Les transactions  du <DatePicker selected={this.state.begin} dateFormat='dd/MM/yyyy' onChange={this._handleBegin} withPortal />
          {' '}au{' '}
          <DatePicker selected={this.state.end} dateFormat='dd/MM/yyyy' onChange={this._handleEnd} withPortal /><br />
          {this._display()}
        </Container>
        {this.state.addMode ? <PopupAddTransaction callback={this._onClose} /> : null}
      </>
    );
  }
}

export default TransactionDetail;