import React from 'react';
import { Button, Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import TableUsers from '../Components/TableUsers';
import PopupAddUser from '../Components/PopupAddUser';
import PopupEditUser from '../Components/PopupEditUser';
import verifyIsAdmin from '../Helpers/verifyIsAdmin';

class Users extends React.Component {

  constructor(props) {
    super(props);
    document.title = 'Le Consulat - Utilisateurs';

    this.state = {
      addMode: false,
      editMode: false,
    }
    this.infoEdit = null;
  }

  componentDidMount() {
    verifyIsAdmin();
  }

  _addUser = () => {
    this.setState({ addMode: true, editMode: false });
  }

  _onUpdate = (info) => {
    this.infoEdit = info;
    this.setState({ addMode: false, editMode: true })
  }

  _onClose = () => {
    this.setState({ addMode: false, editMode: false });
  }

  render() {
    return (
      <>
        <NavBar />
        <Container>
          <h1>Utilisateurs</h1>
          <Button variant='success' onClick={this._addUser}><i class="fas fa-plus-circle"></i> Ajouter un utilisateur</Button>
          <br /><br />
          <TableUsers onUpdate={this._onUpdate} />
          {this.state.addMode ? <PopupAddUser callback={this._onClose} /> : null}
          {this.state.editMode ? <PopupEditUser callback={this._onClose} data={this.infoEdit} /> : null}
        </Container>
      </>
    );
  }
}

export default Users;